* {
    outline: none;
}

.pagination>li.active a {
    background: rgb(16, 166, 216);
}

.pages {
    background: rgb(16, 166, 216);
}

.pagination {
    display: grid;
    grid-template: repeat(auto-fit, minmax(30px, 1fr)) / repeat(auto-fit, minmax(70px, 1fr));
    /* background: rgb(125, 129, 126); */
    grid-gap: 0.5rem;
    margin: 0.5rem 0px;
}

.pagination>li {
    list-style: none;
}

.pagination>li a {
    font-family: 'Varela Round', sans-serif;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-weight: bold;
    background: rgb(125, 129, 126);
    border: 2px solid black;
    border-radius: 1rem;
    padding: 0.3rem 0;
}

.pagination>li a:hover {
    background: rgb(0, 0, 0);
    color: #fff;
    border: 2px solid rgb(125, 129, 126);
}

.alignItem {
    width: 70vw;
    display: grid;
    grid-template: 1fr / 1fr;
}